import React from "react";

const Navbar = () => {
  return (
    <nav className="nav navbar">
      <ul className="nav__list">
        <li className="nav__item">
          <a href="/" className="nav__link">
            Home
          </a>
        </li>
        <li className="nav__item">
          <a href="https://www.github.com/zainsci" className="nav__link">
            Portfolio
          </a>
        </li>
        <li className="nav__item">
          <a href="#about" className="nav__link">
            About Me
          </a>
        </li>
        <li className="nav__item">
          <a href="https://www.twitter.com/zainsci" className="nav__link">
            Contact Me
          </a>
        </li>
      </ul>
    </nav>
  );
};

export default Navbar;
