import React from "react";

import Bio from "./bio";
import Tags from "./tags";

const Footer = () => {
  return (
    <footer className="footer">
      <div className="footer__wrapper">
        <div className="footer__left">
          <Bio />
        </div>
        <div className="footer__right">
          <Tags />
        </div>
      </div>
    </footer>
  );
};

export default Footer;
