import React from "react";
import { Link } from "gatsby";

import Navbar from "./navbar";

const Header = ({ title, isRootPath }) => {
  let header;

  if (isRootPath) {
    header = (
      <div className="logo">
        <div className="logo__border">
          <h1 className="logo__main">
            <Link to="/">{title}</Link>
          </h1>
        </div>
      </div>
    );
  } else {
    header = (
      <div className="logo__link">
        <div className="logo__link__border">
          <Link className="logo__link__main" to="/">
            {title}
          </Link>
        </div>
      </div>
    );
  }
  return (
    <>
      <header className="global__header">{header}</header>
      <div className="container">
        <Navbar />
      </div>
    </>
  );
};

export default Header;
